import React from 'react'
import { CButton } from '@coreui/react'

export const EmojiQuizEndComponent = ({ changeDisplayStatus, score }) => {
  const setDisplayStatus = (number) => {
    {
      changeDisplayStatus(number)
    }
  }

  const isResultDisplay = (
    <div>
      당신의 최종점수는 {score} 입니다!
      <CButton onClick={() => setDisplayStatus(0)}>처음부터</CButton>
    </div>
  )

  return isResultDisplay
}
