import React from 'react'
import { CButton, CRow } from '@coreui/react'

export const EmojiQuizMainComponent = ({ changeDisplayStatus, changeQuizType }) => {
  const setDisplayStatus = (number, string) => {
    changeDisplayStatus(number)
    changeQuizType(string)
  }

  return (
    <div>
      이모티콘퀴즈
      <br />
      이모티콘으로만 제목을 설명할테니 자신있느 장르에 도전해보세요.
      <br />
      <br />
      <CRow className="m-auto mb-2">
        <CButton onClick={() => setDisplayStatus(1, '영화')}>영화</CButton>
      </CRow>
      <CRow className="m-auto mb-2">
        <CButton onClick={() => setDisplayStatus(1, '게임')}>게임</CButton>
      </CRow>
      <br />
      이모티콘 퀴즈는 Chat GPT를 활용한 컨텐츠입니다.
    </div>
  )
}
