import React, { useState, useEffect } from 'react'
import { Timer } from 'src/components/tools/timer'
import { CProgress, CProgressBar } from '@coreui/react'

export const LoadingBar = ({ time, getLoadingBarTimer }) => {
  let timer
  let [timerColor, setTimerColor] = useState('success')
  let [score, setScore] = useState(3)

  let maxTimer = Number(time)
  timer = Timer(Number(time))
  const sendTime = (number) => {
    getLoadingBarTimer(number)
  }

  useEffect(() => {
    if (timer > 10 && timer < 30) {
      setTimerColor('warning')
      setScore(2)
    } else if (timer <= 10) {
      setTimerColor('danger')
      setScore(1)
    }
  })

  useEffect(() => {
    sendTime(timer)
  }, [timer])

  return (
    <CProgress className="mb-3">
      <CProgressBar
        key={timer}
        value={Number(timer / maxTimer) * 100}
        color={timerColor}
        variant="striped"
        animated
      />
      +<strong>{score}</strong>
    </CProgress>
  )
}

export default LoadingBar
