import React, { useState, useEffect } from 'react'
import { fetchEmojiQuiz } from 'src/apis/emoji-quiz'
import { useAsync } from 'react-async'
import { CButton, CCol, CRow, CContainer } from '@coreui/react'
import { LoadingBar } from 'src/components/emoji-quiz/loading-bar'

export const GetEmojiQuiz = (params) => {
  const { data, error, isLoading } = useAsync({
    promiseFn: fetchEmojiQuiz,
    quizType: params.quizType,
    watch: params.score,
  })
  if (isLoading) return 'Loading...'
  if (error) return `Something went wrong: ${error.message}`
  if (data) {
    return data.data
  }
}

export const EmojiQuizGameComponent = ({ changeDisplayStatus, quizType, changeLastScore }) => {
  let [quizNumber, setQuizNumber] = useState(1)
  let [hint1Visable, setHint1Visable] = useState(false)
  let [hint2Visable, setHint2Visable] = useState(false)
  let [hint3Visable, setHint3Visable] = useState(false)
  let [currentTime, setCurrentTime] = useState(60)
  const [score, setScore] = useState(0)

  const setDisplayStatus = (number) => {
    changeDisplayStatus(number)
  }

  const setLastScore = () => {
    changeLastScore(score)
  }
  const getLoadingBarTimer = (time) => {
    time === 30 && setHint1Visable(true)
    time === 10 && setHint2Visable(true)
    setCurrentTime(time)
    if (time === 0) {
      nextQuiz()
      setScore(score - 1)
    }
  }

  const nextQuiz = () => {
    setQuizNumber(quizNumber + 1)
    if (quizNumber === 15) {
      setDisplayStatus(2)
    }
  }

  useEffect(() => {
    setLastScore()
  }, [score])

  useEffect(() => {
    setHint1Visable(false)
    setHint2Visable(false)
    setHint3Visable(false)
  }, [quizNumber])

  let { answer, emojis, genre, hint, hintDetail, quizQuestionList } = GetEmojiQuiz({
    quizType: quizType,
    score,
  })
  let quizList = []

  if (quizQuestionList) {
    for (const quiz of quizQuestionList) {
      quizList.push(
        <CRow key={quiz} className="mb-2">
          <CButton
            key={quiz}
            onClick={() => {
              if (answer === quiz) {
                if (currentTime > 30) {
                  setScore(score + 3)
                } else if (currentTime > 10 && currentTime < 30) {
                  setScore(score + 2)
                } else {
                  setScore(score + 1)
                }
              } else {
                setScore(score - 1)
              }
              nextQuiz()
            }}
          >
            {quiz}
          </CButton>
        </CRow>,
      )
    }
  }

  return (
    <CContainer>
      <CRow className="m-auto mb-4">
        <CCol className="text-right m-auto">
          <strong>점수 : {score}</strong>
        </CCol>
      </CRow>
      <CRow className="m-auto mb-4">
        <CCol className="text-center">
          <strong>
            {quizNumber}. 다음 이모지는 무슨 {quizType} 일까요?
          </strong>
          <h1 style={{ fontSize: '4rem' }}>{emojis}</h1>
        </CCol>
      </CRow>
      <CRow className="m-auto mb-2">
        <CCol className="col-12">{quizList}</CCol>
      </CRow>
      <CRow className="m-auto mb-4">
        <CCol>
          <LoadingBar time={60} key={score} getLoadingBarTimer={getLoadingBarTimer} />
        </CCol>
      </CRow>
      <CRow className="m-auto mb-4">{hint1Visable ? <CCol>{genre}</CCol> : null}</CRow>
      <CRow className="m-auto mb-4">{hint2Visable ? <CCol>{hint}</CCol> : null}</CRow>
      <CRow className="m-auto mb-4">{hint3Visable ? <CCol>{hintDetail}</CCol> : null}</CRow>
    </CContainer>
  )
}

export default EmojiQuizGameComponent
