import React, { useState, useEffect } from 'react'
import { fetchEmojiQuiz } from 'src/apis/emoji-quiz'
import { useAsync } from 'react-async'
import {
  CButton,
  CCard,
  CCardBody,
  CCollapse,
  CCardHeader,
  CCol,
  CRow,
  CProgress,
  CProgressBar,
  CContainer,
} from '@coreui/react'
import { EmojiQuizMainComponent } from 'src/components/emoji-quiz/main'
import { EmojiQuizGameComponent } from 'src/components/emoji-quiz/game'
import { EmojiQuizEndComponent } from 'src/components/emoji-quiz/end'

export const EmojiQuiz = () => {
  const [displayStatus, setDisplayStatus] = useState(0)
  const [quizType, setQuizType] = useState('')
  const [lastScore, setLastScore] = useState()

  const changeDisplayStatus = (number) => {
    setDisplayStatus(number)
  }

  const changeQuizType = (quizType) => {
    setQuizType(quizType)
  }

  const changeLastScore = (score) => {
    setLastScore(score)
  }

  return (
    <CContainer>
      {displayStatus === 0 && (
        <EmojiQuizMainComponent
          changeDisplayStatus={changeDisplayStatus}
          changeQuizType={changeQuizType}
        />
      )}
      {displayStatus === 1 && (
        <EmojiQuizGameComponent
          changeDisplayStatus={changeDisplayStatus}
          quizType={quizType}
          changeLastScore={changeLastScore}
        />
      )}
      {displayStatus === 2 && (
        <EmojiQuizEndComponent changeDisplayStatus={changeDisplayStatus} score={lastScore} />
      )}
    </CContainer>
  )
}

export default EmojiQuiz
