import React, { useState, useEffect } from 'react'

export const Timer = (second) => {
  const [count, setCount] = useState(second)

  useEffect(() => {
    const id = setInterval(() => {
      setCount(count - 1)
    }, 1000)

    if (count === 0) {
      clearInterval(id)
    }

    return () => clearInterval(id)
  }, [count])

  return count
}
